import { Component, OnInit, Input, ElementRef, AfterViewInit, OnDestroy, Directive, TemplateRef, ContentChild, ViewChild, ChangeDetectorRef, ViewEncapsulation} from '@angular/core';
import { FarrisScrollSpyService } from './farris-scrollspy.service';
import { Subject } from 'rxjs';
import { getUuid } from './utils';
import {ComponentTemplateRef}from '@farris/ui-common';

@Directive({
    selector: '[scrollspyItem]'
  })
  export class ScrollspyItemTemplateDirective {
    constructor(public template: TemplateRef<any>) {}
  }

export interface scrollspyData {
    "id": string,
    "title": string,
    "visible"?: boolean
  }
// æ»å¨çå¬åè¡¨ï¼æ¾ç¤ºæ¨¡å¼æ¹åï¼æåºäºä»¶ç»æ
interface SpyListFollowTypeChange{
  prev:string,
  next:string
}

@Component({
  selector: 'farris-scrollspy',
  templateUrl:'./farris-scrollspy.component.html',
  styleUrls:[
    './farris-scrollspy.component.scss'
  ],
  encapsulation: ViewEncapsulation.None
})
export class ScrollspyComponent implements OnInit,AfterViewInit {
    @Input() ctx=null;
    // æ»å¨çå¬é¡¹æ¨¡æ¿
    @Input() scrollspyItemComponentType: ComponentTemplateRef;
    @ContentChild(ScrollspyItemTemplateDirective, {read: TemplateRef}) scrollspyTemplate: TemplateRef<any>;
    /**æ»å¨æ°æ®åè¡¨ */
    // @Input() scrollspyData:scrollspyData[];
    /**å½åactiveé¨åid */
    @Input() currentSectionId:string;
    /**å½åæ»å¨æægroup */
    @Input() scrollGroupId:string = getUuid();
    /**å½åæ»å¨æ¾ç¤ºæ ·å¼ï¼é»è®¤ side å¯éåæ° tabãfixedTab */
    private _followType='side';
    // è®°å½ç¶ææ¹å
    private _emitFollowType:SpyListFollowTypeChange=null;
    @Input() 
    set fFollowType(value:string){
      if(value!==this._followType){
        this._emitFollowType={
          prev:this._followType,
          next:value
        };
        this.changeFollowType();
        this._followType=value;
      }
    }    
    get fFollowType():string{
      return this._followType;
    }

    @Input() sidetabWidth:number = 145;

    @ViewChild("scrollyspyList") scrollyspyListEl;

    _scrollspyData:scrollspyData[];

    @Input()
    set scrollspyData(value){
      if(value){
        this._scrollspyData = value.filter((item) =>{
          if(item.hasOwnProperty('visible') && !item.visible){
            return false
          }
          else{
            return true;
          }
        });
        if(this._scrollspyData && this._scrollspyData.length && !this.currentSectionId ){
          this.currentSectionId = this._scrollspyData[0].id;
        }
        this.scrollSpy.setCurrentSectionIdObj(this.scrollGroupId, this.currentSectionId);
      }
    }
    get scrollspyData(){
      return this._scrollspyData;
    }


    constructor( private elementRef: ElementRef, private scrollSpy: FarrisScrollSpyService,private changeDetector: ChangeDetectorRef, ) { }

    ngOnInit() {
      // if(this.scrollspyData && this.scrollspyData.length && !this.currentSectionId ){
      //   this.currentSectionId = this.scrollspyData[0].id;
      // }
      // this.scrollSpy.setCurrentSectionIdObj(this.scrollGroupId, this.currentSectionId);
      // console.log(this.scrollGroupId);
      this.scrollSpy.setGroupId(this.scrollGroupId);
      this.changeFollowType();
      // è·åå½åSectionçId
      this.scrollSpy.getCurrentSectionId(this.scrollGroupId).subscribe((currentId)=>{
        this.currentSectionId = currentId;
        this.scrollSpy.updateScrollSpiesActiveClsName(this.scrollGroupId,currentId);
      });

      // è·åå¯¹åºContaineråºåï¼å¨æ»å¨æ¶çå½åSectionçåå
      this.scrollSpy.updateScrollGroup(this.scrollGroupId).subscribe((currentSection: string): void => {

          this.scrollSpy.setCurrentSectionId(this.scrollGroupId,currentSection);
          
        });
    }
    // æ¹åFollowTypeæ¶
    changeFollowType(){
      if(this.scrollGroupId&&this._emitFollowType){
        this.scrollSpy.changeGroupIdFollowType(this.scrollGroupId,this._emitFollowType);
        this._emitFollowType=null;
      }
    }
    ngAfterViewInit(): void {
        /**å½æ»å¨ä½ç½®ä¸ºsideæ¶ï¼è¯¥ç»ä»¶æè¿ç¶èç¹æ·»å relativeå®ä½ */
        const parent = this.elementRef.nativeElement.parentElement;
        if(!parent.style.position && parent.style.position !== 'relative' && this.fFollowType === 'side'){
            parent.style.position = 'relative';
        }
        // if(this.scrollspyData && this.scrollspyData.length && this.currentSectionId){
        //   if(this.currentSectionId !== this.scrollspyData[0].id){
        //     console.log(222);
        //     const element = this.scrollyspyListEl.nativeElement.querySelector("#scrollspy-"+this.currentSectionId);
        //     const follow = {
        //       element: element,
        //       id: this.currentSectionId
        //     };
        //     const groupId = this.scrollGroupId ? this.scrollGroupId: 'default';
        //     this.scrollSpy.fScrollFollowClick(groupId,follow);
        //   }
        // }
        
    }

}