import { Directive, Injectable, Input, EventEmitter, Output, ElementRef, OnInit, OnDestroy, AfterViewInit, ChangeDetectorRef, HostBinding } from '@angular/core';
import { FarrisScrollSpyService } from './farris-scrollspy.service';
//éç¹é¨å
@Directive({
    selector: '[fScrollSpy]'
})
export class FarrisScrollSpyDirective implements OnInit,AfterViewInit, OnDestroy {
    @Input('fScrollSpy') scrollId: string;
    @Input('fScrollGroup') scrollGroupId: string;
  
    constructor(
      private elementRef: ElementRef,
      private scrollSpy: FarrisScrollSpyService,
    ) {}
  
    public ngOnInit(): void {
      this.elementRef.nativeElement.className+=' f-scrollspy-part';
      if(!this.scrollGroupId){
        this.scrollGroupId = this.scrollSpy.getGroupId();
      }
      //this.scrollSpy.addElement(this.elementRef.nativeElement, this.scrollId,this.scrollGroupId);
    }
    ngAfterViewInit(){
      this.scrollSpy.addElement(this.elementRef.nativeElement, this.scrollId,this.scrollGroupId);
    }
  
    public ngOnDestroy(): void {
      this.scrollSpy.removeElement(this.scrollId, this.scrollGroupId);
    }
  }
//è·éç¹å»
@Directive({
    selector: '[fScrollFollowElement]'
})
export class FscrollFollowElementDirective implements OnInit, OnDestroy {

    //@Input() followId: string;
    @Input('fScrollFollowElement') followId:string;
    @Input('fScrollElement') followGroupId:string;
    constructor(
      private elementRef: ElementRef,
      private scrollSpy: FarrisScrollSpyService,
    ) {}
  
    public ngOnInit(): void {
      if(!this.followGroupId){
        this.followGroupId = this.scrollSpy.getGroupId();
      }
      this.scrollSpy.addFollowElement(this.elementRef.nativeElement, this.followId, this.followGroupId);
    }

    public ngOnDestroy(): void {
      this.scrollSpy.removeFollowElement(this.followId, this.followGroupId);
    }

  }

